body {
    margin: 0 auto;
}

.base {
    display: flex;

    min-height: 100vh;

    font-family: Calibri, serif;
}

nav {
    width: 16.5%;
}

nav header {
    height: 5%;

    display: flex;
    align-items: center;
    justify-content: center;
}

nav header button {
    border: none;
    background: none;

    cursor: pointer;
}

nav header button h3 {
    margin: 0;
}

nav span {
    display: flex;
    justify-content: center;
    align-items: center;
    color: #808080b0;
    font-weight: bold;
}

.chatList span {
    opacity: 0;
    animation: load .5s forwards;
}

@keyframes load {
    0% {opacity: 0;}
    100% {opacity: 1;}
}

.searchBar {
    transition: all .3s cubic-bezier(0, 0.02, 0.44, 1.01);

    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: nowrap;
    flex-direction: row-reverse;

    padding: 5px;
    border-radius: 15px;
    background: black;
    margin-bottom: .5rem;
}

.searchBar input::-webkit-search-cancel-button {
    display: none;
}

.searchBar * {
    transition: all .3s cubic-bezier(0, 0.02, 0.44, 1.01);
}

.searchBar input {
    border: none;
    font-size: 18px;
    width: 100%;
    color: white;
    margin-left: .5rem;
    background: black;
}

.searchBar svg {
    color: white;
}

.searchBar input:focus {
    outline-width: 0;
}

.searchBar input:focus ~ .searchBar {
    background: white;
}

.searchBarSelected {
    transition: all .3s cubic-bezier(0, 0.02, 0.44, 1.01);

    outline: 2px solid black;
    background: white;
}

.searchBarSelected svg {
    color: black;
}

.searchBarSelected input {
    color: black;
    background: white;
}

.chat {
    transition: all 1s cubic-bezier(0, 0.02, 0.44, 1.01);

    width: 100%;

    box-sizing: border-box;

    border: 2px solid black;
    padding: 2.5% 2%;
}

.chat:hover {
    background-color: #e5e5e5;
    cursor: pointer;
}

.chat:focus {
    background-color: #e5e5e5;
}

.chat:not(:first-child) {
    border-top: none;
}

.chat div {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: nowrap;
    flex-direction: row;
}

.chat div h4, p {
    margin: 0;
}

.chat div h4 {
    white-space: pre;
    overflow: hidden;
    text-overflow: ellipsis;
}

.chat p {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;

    color: #555555;
}

.time {
    font-size: 80%;
}

main {
    height: 100vh;
    width: 80%;

    padding: 0 8% 1.5% 8%;

    display: flex;
    flex-wrap: nowrap;
    justify-content: flex-end;
    flex-direction: column;

    box-sizing: border-box;
}

main .messageContainer {
    display: flex;
    flex-direction: column-reverse;
    flex-wrap: nowrap;

    overflow-y: scroll;

    padding: .5% 5% 0 5%
}

#messageField {
    background-color: #555555;
}

.messageOwn, .messageInterlocutor {
    font-family: Calibri, sans-serif;
    border: 2px solid black;

    white-space: pre-line;

    width: fit-content;

    max-width: 50%;

    padding: 5px 15px;

    border-radius: 20px;

    margin: .5% 0;
}

.messageOwn {
    margin-right: 0;
    margin-left: auto;

    background-color: black;
    color: white;
}

.messageOwn span, .messageInterlocutor span {
    bottom: auto !important;
    position: relative;
    font-size: 10px;
    margin-left: .5rem;
    float: right;
    top: .5rem;
    right: -.3rem;
}

main footer {
    margin-top: 2%;
    display: flex;

    align-items: center;
}

main footer svg:hover {
    cursor: pointer;
}

main footer * {
    border-radius: 20px;
}

main footer .messageInput {
    border: 2px solid black;
    width: 85%;
    margin-right: .75rem;
    padding: 7px 15px 7px 15px;
    max-height: 26.5rem;
    overflow-y: auto;
}

main footer button {
    font-family: Calibri, sans-serif;

    font-size: 20px;
    font-weight: bold;

    background: black;
    color: white;

    border: none;

    width: 12.5%;

    padding: .5% 0 .5% 0;

    height: fit-content;
}

main footer button:hover {
    cursor: pointer;
}